import React from "react";

export function TwoCols({ children, ...props }) {
  let elements = React.Children.toArray(children);

  return (
    <div className="two-cols" {...props}>
      <div className={`col-left ${props.className}`}>{elements[0]}</div>
      <div className={`col-right ${props.className}`}>{elements[1]}</div>
    </div>
  );
}

export function Flex(props) {
  return (
    <div
      style={{
        display: "flex",
        justifyContent: "space-around",
        margin: "1rem",
      }}
    >
      {props.children}
    </div>
  );
}

export function Card(props) {
  return (
    <div className="card">
      {props.title ? <div className="card-title">{props.title}</div> : null}
      <div className="card-body">{props.children}</div>
    </div>
  );
}
