import React from "react";

import { MDXProvider } from "@mdx-js/react";

const fragmentListItem = (li) => {
  return React.cloneElement(li, { className: "fragment" });
};

const ulFragment = ({ children, ...props }) => {
  return <ul>{React.Children.map(children, fragmentListItem)}</ul>;
};

const olFragment = ({ children, ...props }) => {
  return <ol>{React.Children.map(children, fragmentListItem)}</ol>;
};

const tableFragment = ({ children, ...props }) => {
  let newChildren = React.Children.map(children, (child) => {
    if (child.props.originalType === "tbody") {
      let newBody = React.Children.map(child.props.children, (row) => {
        let newRow = React.Children.map(row.props.children, (cell) => {
          return React.cloneElement(cell, { className: "fragment" });
        });
        return React.createElement("tr", {}, newRow);
      });
      return React.createElement("tbody", {}, newBody);
    } else {
      return child;
    }
  });
  return React.createElement("table", {...props}, newChildren);
};

const components = {
  ul: ulFragment,
  ol: olFragment,
  table: tableFragment,
};

export function Animate({ children, ...props }) {

  let updatedChildren = React.Children.map(children, (child) => {
    return React.cloneElement(child, { className: child.props.className + " fragment" });
  });

  return (
    <MDXProvider {...props} components={components}>
      {updatedChildren}
    </MDXProvider>
  );
}
