import React from "react";

export function Example(props) {
  let classes = "example";
  classes = props.animate ? classes + " fragment" : classes;

  return (
    <div {...props} className={classes}>
      {props.children}
    </div>
  );
}

export function Meta(props) {
  let classes = "meta";
  classes = props.animate ? classes + " fragment" : classes;

  return (
    <div {...props} className={classes}>
      {props.children}
    </div>
  );
}

export function Tooltip(props) {
  let classes = "tooltip";
  classes = props.animate ? classes + " fragment" : classes;

  const bulb =
    "data:image/svg+xml;base64,";
  return (
    <div {...props} className={classes}>
      <img alt="lightbulb" src={bulb} />
      <div>
        {props.notitle ? "" : <div className="tooltip-title">¡Ojo!</div>}
        {props.children}
      </div>
    </div>
  );
}

export function Definition(props) {
  let classes = "definition";

  classes = props.animate ? classes + " fragment" : classes;

  return (
    <div {...props} className={classes}>
      <span className="definitionWord">{props.word}:</span> {props.children}
    </div>
  );
}

export function Person() {
  return (
    <img
      style={{ maxHeight: "250px", maxWidth: "100px" }}
      alt="person"
      src="data:image/svg+xml;base64,"
    />
  );
}

export function FQ(props) {
  return "«" + props.children + "»"
}